@font-face {
  font-family: "oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff") format("woff"), url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  padding: 0;
  border: 0;
  margin: -1px;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.button {
  display: flex;
  justify-content: center;
  min-width: 168px;
  min-height: 40px;
  background-color: #68b738;
  font-family: "oswald", "Tahoma", sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  align-items: center;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-style: none;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.page {
  margin: 0;
