.cover {
  position: relative;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-backgrond-cat/cat-background-mobile.jpg");
  background-repeat: no-repeat, no-repeat;
  background-size: 100% 302px, 320px 302px;
  background-position: top center;
}
@media (min-resolution: 2dppx) {
  .cover {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-backgrond-cat/cat-background-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .cover {
    background-image: none;
  }
}
@media (min-width: 1280px) {
  .cover {
    margin-bottom: 35px;
  }
}

@media (min-width: 1280px) {
  .cover::before {
    position: absolute;
    content: "";
    height: 694px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-backgrond-cat/cat-background-desktop.jpg");
    background-repeat: no-repeat, no-repeat;
    background-size: auto 694px;
    background-position: top left;
    right: 0;
    left: calc((100% - 1220px) / 2 + 610px);
    top: -189px;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .cover::before {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index-backgrond-cat/cat-background-desktop@2x.jpg");
  }
}

